import React from "react";
import { Container, DownloadButtonLink, IconContainer } from "./ButtonElements";

const DownloadCVButton = () => {
  return (
    <>
      <Container>
        <DownloadButtonLink href="./ShashwatResume.pdf" download>
          Download CV
        </DownloadButtonLink>
      </Container>
    </>
  );
};

export default DownloadCVButton;
